<template>
<div>
<swiper :options="swiperOption" ref="mySwiper">
  <!--  -->
  <swiper-slide><img src="../assets/image/ban1.jpg"/></swiper-slide>
  <swiper-slide><img src="../assets/image/ban2.jpg"/></swiper-slide>
  <swiper-slide><img src="../assets/image/ban3.jpg"/></swiper-slide>
  <!--  -->
  <div class="swiper-pagination" slot="pagination"></div>
<!--  <div class="swiper-button-prev" slot="button-prev"></div>
  <div class="swiper-button-next" slot="button-next"></div>
  <div class="swiper-scrollbar"  slot="scrollbar"></div>-->
</swiper>

</div>
</template>
<style>
	.swiper-container{
		width: 100%;
		height: 0;
		padding-bottom: 27%;	
	} 
	.swiper-container img{
		width: 100%;
		height: 100px;
	}
</style>
<script>  
    import { swiper, swiperSlide } from 'vue-awesome-swiper'  
    export default {  
        components: {  
            swiper,  
            swiperSlide  
        },  
        data() {  
            return {  
                swiperOption: {  
                //notNextTicktrueNextTickswiperswiperswipertrue  
                notNextTick: true,  
                autoplay: 2000,
                msg:'',
                pagination: '.swiper-pagination',  
                slidesPerView: 'auto',  
                centeredSlides: true,  
                paginationClickable: true,  
                spaceBetween: 30,  
                    onSlideChangeEnd: swiper => {  
                        //swiper  
                        this.page = swiper.realIndex+1;  
                        this.index = swiper.realIndex;  
                    }  
                }  
            }  
        },  
        //sweiper  
        computed: {  
  
            swiper() {  
              return this.$refs.mySwiper.swiper;  
            }  
        },  
        mounted () {  
            //swiperswiper  
            this.swiper.slideTo(0, 0, false);  
        },
        created() {
      //
        this.$http.get('demo.json').then((res) => {
      	this.msg = res.body.swiper
      	console.log(res.body.swiper)
//      this.tranform = false;
      }, (error) => {
        console.log(error)
      })
    }        
  
    }  
</script>  


