      },
      methods:{
        //
        newDate(){
          let myDate = new Date();
          let year = 1900+myDate.getFullYear();
          let month = myDate.getMonth()+1;
          if(month<10){
            month = "0" + month
          }
          let day = myDate.getDate();
          if(day<10){
            day = "0" + day
          }
          this.ymd = year+"-"+month+"-"+day;
          // console.log(this.ymd);
        }
      },
      components:{
        backbar
//      spinner
      }
    }
</script>



