	}
	ul li:nth-last-child(1){
		margin-bottom: 56px;
	}
	ul .hotBox{
		margin: 3% 5%;
	}
	.hotBox #left{
		display: inline-block;
		height: 40px;
		width: 40px;
		border-radius: 50%;
		float: left;
	}
	.hotBox #right{
		float: left;
		margin-left: 20px;
	}
	.hotBox #right p:nth-of-type(2){
		padding-top: 5px;
		font-size: 10px;
		color: #818181;
	}
	.hotBox #bottom{
		clear: both;
		margin-left: 60px;
	}
	.hotBox #bottom p{
		font-size: 14px;
		color: #333;
		line-height: 20px;
	}
	.hotBox img{
		border-radius: 50%;
		width: 100%;
		height: 100%;
	}
	
</style>


